@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;600&family=Open+Sans:ital,wght@0,300;0,400;0,600;1,300&family=Poppins:wght@300;400;500;600&family=Roboto:wght@300&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

:root .light {
    --color-bg: #FFCC3D;
    --color-bg-variant: #fdcf50;
    --color-primary: #ffa341;
    --color-primary-variant: rgba(177, 140, 7, 0.4);
    --color-white: #419bf9;
    --color-light: rgba(72, 72, 72, 0.6);
}

:root .dark {
    --color-bg: #1f1f38;
    --color-bg-variant: #2c2c6c;
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);
}

.light {
    background: white;
    color: black;
    transition: var(--transition);
}

.dark {
    background: #1f1f38;
    color: white;
    background-image: url(./assets/bg-texture.png);
    transition: var(--transition);
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    line-height: 1.7;
}

/* ========== GENERAL STYLES ========== */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* ========== MEDIA QUERIES (MEDIUM) ========== */

@media screen and (max-width: 1042px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* ========== MEDIA QUERIES (SMALL) ========== */

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-ms);
    }

    section > h2{
        margin-bottom: 2rem;
    }
}